body {
  color: #fff;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

canvas {
  -webkit-user-select: none;
  user-select: none;
  outline: none;
  display: block;
}

#ui {
  pointer-events: none;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

#ui > * {
  pointer-events: all;
}

#ui #toolbar {
  -webkit-user-select: none;
  user-select: none;
  display: flex;
}

#ui #toolbar button {
  color: #fff;
  cursor: pointer;
  background-color: #18181821;
  border: none;
  padding: 16px;
  transition: background-color .2s ease-out;
  display: block;
}

#ui #toolbar button:hover {
  background-color: #1818187a;
}

#ui #toolbar button:active {
  background-color: #181818b9;
}

#ui #lobby-controls {
  width: 250px;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#ui input.close {
  color: #fff;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  background-color: #0000;
  border-color: #0000;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  font-size: 1rem;
  font-weight: bold;
}

#ui input.close:hover {
  background-color: #80808054;
  border: 2px solid gray !important;
}

#ui .dialog {
  background-color: #181818;
  border-radius: 10px;
  width: 250px;
  margin: auto;
  padding: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#ui .dialog h2 {
  border-bottom: 1px solid gray;
  margin-top: 0;
}

#ui .dialog .field {
  margin-bottom: .2rem;
}

#ui .dialog button.submit {
  cursor: pointer;
  color: #fff;
  background-color: #1c851c;
  border-color: #0000;
  border-radius: 4px;
  margin-top: 1rem;
  padding: 4px 16px;
  font-size: 1.5rem;
}

#ui .dialog button.submit:hover {
  background-color: #23a523;
}

#ui .dialog button.submit:active {
  background-color: #166816;
}
